.social {
	display: flex;
	flex-wrap: wrap;

	&__item {
		flex-shrink: 0;

		svg {
			fill: var(--dark);
			width: 2.6rem;
			height: 2.6rem;
			transition: $transition fill;
		}

		@media (any-hover: hover) {
			&:hover {
				svg {
					fill: var(--red);
				}
			}
		}
	}

	&__link {
		@extend %flex-center;
		width: 100%;
		height: 100%;
	}
}
