.pickup-popup {
	overflow-x: hidden !important;

	@media(min-width: 1025px) {
		width: 103rem;
	}

	@media(max-width: 1024px) {
		width: 100vw;
	}

	@media(max-width: 1024px) {
		min-height: 100vh;
	}

	&::-webkit-scrollbar {
		width: 0;
		-webkit-appearance: none;
	}

	// .pickup-popup__inner

	&__inner {
		display: grid;

		@media(max-width: 1024px) {
			gap: var(--offset-ls);
		}

		@media(max-width: 500px) {
			gap: 1.5rem;
		}

		@media(min-width: 1025px) {
			grid-template-columns: 31rem 1fr;
		}
	}

	&__body {
		@media(max-width: 1024px) {
			width: 100vw;
		}
	}

	&__title {
		@extend %text-20;
		font-weight: 500;
		padding: 2.5rem 2.5rem 1rem 2.5rem;
	}

	// .pickup-popup__shops

	&__shops {
		height: 74rem - 9.3rem - 6.5rem;
		overflow-y: auto;

		@media(min-width: 768px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
		}

		@media(min-width: 1025px) {
			display: block;
			@include scrollbar;
		}
	}

	// .pickup-popup__map

	&__map {
		position: relative;

		@media(max-width: 1024px) {
			height: 30rem;
		}
	}

	&__map-body {
		position: relative;
		z-index: 25;
		width: 100%;
		height: 100%;
		max-height: 74rem;
	}

	// .pickup-popup__search

	&__search {
		position: absolute;
		top: var(--offset-ls);
		left: var(--offset-ls);
		z-index: 30;
	}
}
