.promo-collection {
	display: grid;
	background-color: var(--gray-bg);
	grid-template-rows: 5rem max-content 2.5rem max-content auto max-content 5.3rem;
	grid-template-columns: 12.9rem auto var(--offset-ls) 50%;
	grid-template-areas: '. . . image'
		'. suptitle . image'
		'. . . image'
		'. title . image'
		'. . . image'
		'. link . image'
		'. . . image';

	@media (max-width: 1024px) {
		grid-template-rows: var(--offset-ls) max-content 1.5rem max-content auto max-content var(--offset-ls);
		grid-template-columns: var(--offset-ls) auto var(--offset-ls) 50%;
	}

	@media (max-width: 767px) {
		grid-template-rows: 14rem var(--offset-ms) max-content var(--offset-xs) max-content 2.6rem max-content var(--offset-ms);
		grid-template-columns: var(--offset-ms) 1fr var(--offset-ms);
		grid-template-areas:
			'image image image'
			'. . .'
			'. suptitle .'
			'. . .'
			'. title .'
			'. . .'
			'. link .'
			'. . .';
	}

	@media (min-width: 768px) {
		min-height: 35rem;
	}

	// .promo-collection__suptitle

	&__suptitle {
		font-weight: 500;
		color: var(--gray-777);
		grid-area: suptitle;

		@media (max-width: 500px) {
			font-size: 1.2rem;
			line-height: 1.8rem;
		}
	}

	// .promo-collection__title

	&__title {
		grid-area: title;
	}

	&__link {
		grid-area: link;
	}

	// .promo-collection__img

	&__img {
		grid-area: image;

		img {
			@extend %cover;
		}
	}
}
